import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountService, AlertService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-contactus',
  templateUrl: './contactus.component.html',
  styleUrls: ['./contactus.component.less']
})
export class ContactusComponent implements OnInit {

  add: FormGroup;
  id: string;
  loading = false;
  submitted = false;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
    private alertService: AlertService
  ) { }

  // add = new FormGroup({
    
  //   firstName: new FormControl('',[Validators.required]),
  //   lastName: new FormControl('',[Validators.required]),
  //   mobileNo: new FormControl('',[Validators.required]),
  //   emailAddress: new FormControl('',[Validators.required]),
  //   subject: new FormControl('',[Validators.required]),
  //   message: new FormControl('',[Validators.required]),
  // })


  ngOnInit(): void {
    this.add = this.formBuilder.group({
      firstName: ['', [Validators.required]],
      lastName: ['',[Validators.required]],
      mobileNo: ['', [Validators.required]],
      emailAddress: ['',[Validators.required,Validators.email]],
      subject: ['', [Validators.required]],
      message: ['',[Validators.required]],
  });
  }
  get f() { return this.add.controls; } 

  onSubmit() {
    debugger;
    this.submitted = true;

    // reset alerts on submit
    this.alertService.clear();

    // stop here if form is invalid
    if (this.add.invalid) {
      return;
    }
    
      console.warn(this.add.value)
      debugger;
      this.loading = true;
      this.accountService.contactusEmailSend(this.add.value)
        .pipe(first())
        .subscribe({
          next: () => {
            this.alertService.success('Thank you, we will contact you shortly.', { keepAfterRouteChange: true });
            // this.router.navigate(['/admin/career'], { relativeTo: this.route });
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
     


    // Swal.fire(
    //   'Done!',
    //   'Your record has been successfully added',
    //   'success'
    // )



  }

}
