import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { AccountService } from '@app/_services';

@Component({
  selector: 'app-portfolio-detail',
  templateUrl: './portfolio-detail.component.html',
  styleUrls: ['./portfolio-detail.component.less']
})
export class PortfolioDetailComponent implements OnInit {

  portfolioData:any;
  constructor(private router:ActivatedRoute,private accountService:AccountService) { }

  ngOnInit(): void {
    this.accountService.PortfolioById(this.router.snapshot.params.id).subscribe((portfolioData: any) => {
      console.warn(portfolioData);
      this.portfolioData = portfolioData;

    })
  }

}
