import { Component, OnInit } from '@angular/core';
import { ActivatedRoute, NavigationEnd, Router } from '@angular/router';
import { AccountService } from '@app/_services';

@Component({
  selector: 'app-blog-detail',
  templateUrl: './blog-detail.component.html',
  styleUrls: ['./blog-detail.component.less']
})
export class BlogDetailComponent implements OnInit {

  blogData: any = [];
  blogFilterdata: Array<any> = [];
  blogTempdata: Array<any> = [];
  TagData: any=[];
  ResentPost: any = [];
  mySubscription: any;

  constructor(private router:ActivatedRoute, private accountService: AccountService, private route: Router) {

    this.route.routeReuseStrategy.shouldReuseRoute = function () {
      return false;
    };
    this.mySubscription = this.route.events.subscribe((event) => {
      if (event instanceof NavigationEnd) {
        // Trick the Router into believing it's last link wasn't previously loaded
        this.route.navigated = false;
      }
    });
   }

ngOnDestroy() {
  if (this.mySubscription) {
    this.mySubscription.unsubscribe();
  }
}


  ngOnInit(): void {
    console.warn(this.router.snapshot.params.id);
    this.accountService.BlogById(this.router.snapshot.params.id).subscribe((blogData) => {
      console.warn(blogData);
      this.blogData = blogData;
      // this.blogTempdata = this.blogData;
    })
    // tag
    this.accountService.getTagListData().subscribe((TagData) => {
      console.warn(TagData);
      this.TagData = TagData;
      // this.TagTempdata = this.TagData;
    })
    // resent Post
    this.accountService.BloggetAll1().subscribe((ResentPost) => {
      console.warn(ResentPost);
      this.ResentPost = ResentPost;
      // this.blogTempdata = this.blogData;
    })



  }

}
