import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { AccountService } from '@app/_services';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-portfolio',
  templateUrl: './portfolio.component.html',
  styleUrls: ['./portfolio.component.less']
})
export class PortfolioComponent implements OnInit {

  users: any = null;
  name: any;
  value: any;
  selectvalue = "Choose...";
  page:any = 1;
  count:any = 0;
  pageSize:any = 9;
  pageSizes:any = [9, 10, 25];
  pagecount:any ="Selecte Page per Recode";
  pageIndex:any =1;
  TagData: any = [];
  TagFilterdata: Array<any> = [];
  TagTempdata: Array<any> = [];
  x=1
  Category:any;
  constructor(private accountService: AccountService, private route: Router ) { }
  PageSizeCount(){
    this.pageSize=this.pagecount
  }
  getRequestParams(page, pageSize): any {
    // tslint:disable-next-line:prefer-const
    let params = {};

    if (page) {
      params[`page`] = page;
    }

    if (pageSize) {
      params[`size`] = pageSize;
    }

    return params;
  }
  ngOnInit() {

    this.accountService.getPortfolioListData().subscribe((data) => {
      debugger;
      console.warn(data);
      this.TagData = data;
      // this.TagTempdata = this.TagData;
    })
    this.accountService.BlogCategorygetAll1().subscribe((Category) => {
      debugger;
      console.warn(Category);
      this.Category = Category;
      // this.TagTempdata = this.TagData;
    })
  }
 


  //pagination
  p: number = 1;
  // sorting
  reverse: boolean = false;
  key = 'TagId';
  sort(key: string) {
    this.key = key;
    this.reverse = !this.reverse;
  }


}
