import { Component, OnInit } from '@angular/core';
import { resetFakeAsyncZone } from '@angular/core/testing';
import { FormBuilder, FormControl, FormGroup, Validators } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { AccountService, AlertService } from '@app/_services';
import { first } from 'rxjs/operators';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-careers-form',
  templateUrl: './careers-form.component.html',
  styleUrls: ['./careers-form.component.less']
})
export class CareersFormComponent implements OnInit {
  careerData:any;
  form: FormGroup;
  id: string;
  loading = false;
  // submitted = false;
  careerId:string;
  submitted = false;

  constructor(
    private formBuilder: FormBuilder,
    private route: ActivatedRoute,
    private router: Router,
    private accountService: AccountService,
    private alertService: AlertService
  ) { }
  add = new FormGroup({
    firstName: new FormControl('',[Validators.required]),
    lastName: new FormControl('',[Validators.required]),
    mobileNo: new FormControl('',[Validators.required]),
    emailAddress: new FormControl('',[Validators.required,Validators.email]),
    birthdate: new FormControl('',[Validators.required]),
    gender: new FormControl('',[Validators.required]),
    experience: new FormControl('',[Validators.required]),
    location: new FormControl('',[Validators.required]),
    subject: new FormControl('',[Validators.required]),
    resume: new FormControl('',[Validators.required]),
    messsage: new FormControl('',[Validators.required]),
  })

  get f() { return this.add.controls; } 

  ngOnInit(): void {
    this.accountService.careerById(this.route.snapshot.params.id).subscribe((careerData: any) => {
      console.warn(careerData);
      this.careerData = careerData;
    })
  }
  onSubmit() {
    debugger;
    this.submitted = true;

    // reset alerts on submit
    this.alertService.clear();

    // stop here if form is invalid
    if (this.add.invalid) {
      return;
    }

      console.warn(this.add.value)
      debugger;
      this.loading = true;
      this.accountService.careerFormEmailSend(this.add.value)
        .pipe(first())
        .subscribe({
          next: () => {
            debugger
            this.alertService.success('Email send successfully', { keepAfterRouteChange: true });
            // this.router.navigate(['/admin/career'], { relativeTo: this.route });
            
          },
          error: error => {
            this.alertService.error(error);
            this.loading = false;
          }
        });
        this.add.reset({})
  }
}
